import { access_records, access_records_del } from '@/http/api.js';
import tuiLoadmore from '@/plugins/thorui/components/loadmore/loadmore.vue';
import tuiNomore from '@/plugins/thorui/components/nomore/nomore';
export default {
	components: {
		tuiLoadmore,
		tuiNomore
	},
	data() {
		return {
			list: [],
			page: 1,
			loadding: false,
			pullUpOn: true,
			openid: ''
		};
	},
	onShow() {
		wx.hideHomeButton();
		let pages = getCurrentPages();
		let currPage = pages[pages.length - 1]; //当前页面
	},
	onLoad(options) {
		if (uni.getStorageSync('openid')) {
			this.openid = uni.getStorageSync('openid');
		}
		this.getList();
	},
	methods: {
		getList() {
			access_records(this.openid).then(res => {
				if (!this.pullUpOn) return;
				this.loadding = true;
				if (res.data.data.length == 0) {
					this.loadding = false;
					this.pullUpOn = false;
					return;
				}
				if (res.data.current_page == res.data.last_page) {
					this.loadding = false;
					this.pullUpOn = false;
					this.list = this.list.concat(res.data.data);
					this.page = this.page + 1;
					return;
				}
				this.list = this.list.concat(res.data.data);
				this.page = this.page + 1;
				console.log(this.list);
			});
		},
		gotoStudio(id) {
			uni.navigateTo({
				url: '/pages/studio/studio?id=' + id
			});
		},
		access_records_del(id) {
			access_records_del(id).then(res => {
				uni.showToast({
					// icon: 'none',
					title: '删除成功'
				});
				this.list = [];
				this.pullUpOn = true;
				this.page = 1;
				this.getList();
				console.log(this.list);
			});
		}
	},
	// 页面上拉触底事件的处理函数
	onReachBottom: function() {
		this.getList();
	}
};
